import uniNavBar from "../../../components/uni-nav-bar/uni-nav-bar.vue"
export default {
	components: {
		uniNavBar
	},
	data() {
		return {
			swiperheight:0,
			tabIndex:0,
			tabBar:[
				{name:"进行中",id:"going"},
				{name:"已完成",id:"completed"}
			],
			tslist:[
				{
					tsshop:"BbcMaLL直营店",
					txzhuangtai:"已完成",
					txpic:"../../../static/goods/p1.jpg",
					tsname:"按实际对话框flash打飞机萨拉款到发货卢萨卡的解放后",
					txyy:"售后保障服务",
					times:"2018-02-12"
				},
				{
					tsshop:"BbcMaLL直营店",
					txzhuangtai:"已完成",
					txpic:"../../../static/goods/p2.jpg",
					tsname:"按实际对话框flash打飞机萨拉款到发货卢萨卡的解放后",
					txyy:"售后保障服务",
					times:"2018-02-12"
				}
			]
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res)=> {
				let height = res.windowHeight - uni.upx2px(125);
				this.swiperheight = height
			}
		});
	},
	methods: {
		back(){
			uni.navigateBack({
				delta: 1
			})
		},
		tabTabs(index){
			this.tabIndex=index;
		},
		tabChange(e){
			uni.showLoading();
			this.tabIndex=e.detail.current;
			uni.hideLoading();
		},
		toShow(){
			uni.navigateTo({
				url: 'complaintshow',
				
			});
		}
	}
}
